.form {
  width: 327px;

  @media (max-width: $max-tablet-width) {
    width: 327px;
    padding: 80px 0 70px;
  }

  @media (max-width: $max-tablet-width) {
    width: 321px;
    padding: 70px 16px;
  }

  @media (max-width: $max-mobile-width) {
    width: auto;
  }
}

.form__desc {
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  color: $white;
  letter-spacing: -0.005em;

  @media (max-width: $max-tablet-width) {
    color: $black;
    font-size: 18px;
    margin-bottom: 30px;
  }

  @media (max-width: $max-mobile-width) {
    margin-bottom: 20px;
  }
}

.form__label {
  display: block;
  margin: 20px 0;
}

.form__input {
  display: block;
  width: 100%;
  height: 48px;
  box-sizing: border-box;
  padding: 13px 29px 11px;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  border-radius: 6px;
  border: 1px solid $input-border;

  @media (max-width: $max-tablet-width) {
    padding: 11px 21px;
  }
}

.form__submit {
  display: block;
  width: 100%;
  background-color: $bright;
  text-align: center;
  font-weight: 600;
  font-size: 15px;
  line-height: 51px;
  border: none;
  cursor: pointer;
  border-radius: 6px;

  &:hover,
  &:active {
    opacity: 0.85;
  }

  &:focus {
    opacity: 0.7;
  }
}
